body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgba(255, 215, 170, 1), rgba(212, 127, 106, 1));
  background: linear-gradient(to right, rgba(255, 215, 170, 1), rgba(212, 127, 106, 1));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    background: rgba(255, 215, 170, .5);
}
.search {
    border: solid 1px rgb(128, 42, 21);
    background: rgba(255, 215, 170, 0.6);
    
}
.scrollBox {
    overflow-y: scroll;
}
